import * as React from "react"
import "../css/index.css"
import {useEffect, useState} from "react";


const IndexPage = () => {
    const [fallingDivs, setFallingDivs] = useState([]);
    const [divCount, setDivCount] = useState(10); // 생성할 요소 수

    useEffect(() => {
        // divCount 수만큼 무작위 위치에서 떨어지는 요소를 생성
        const newFallingDivs = [];
        for (let i = 0; i < divCount; i++) {
            newFallingDivs.push({
                id: i,
                left: Math.random() * 100 + 'vw', // 수평 위치 (무작위)
                delay: Math.random() * 3, // 시작 지연 시간 (무작위)
            });
        }
        setFallingDivs(newFallingDivs);

        // 요소가 떨어지고 나면 주기적으로 다시 시작하도록 설정
        const interval = setInterval(() => {
            setFallingDivs((prevDivs) => {
                return prevDivs.map((div) => ({
                    ...div,
                    left: Math.random() * 100 + 'vw',
                    delay: Math.random() * 3,
                }));
            });
        }, 3000); // 3초마다 다시 시작

        return () => clearInterval(interval);
    }, [divCount]);

    return (
        <div className="falling-text-container">
            {fallingDivs.map((div) => (
                <div
                    key={div.id}
                    className="falling-div"
                    style={{
                        left: div.left,
                        animationDelay: `${div.delay}s`,
                    }}
                >
                    박소현
                </div>
            ))}
        </div>
    );
}

export default IndexPage

export const Head = () => <title>SOHNA-48</title>
